export const button = cva(
  // base classes
  [
    'zep-typography-button',
    'zep-rounded-button',
    'zep-px-1', // fixme with spacing.button.paddingX
    'zep-py-0.75', // fixme with spacing.button.paddingY
    'zep-flex',
    'zep-gap-0.5', // fixme with spacing.button.gap
    'zep-outline-none',
    // fixme: create and use .zep-focusVisible component via addComponents plugin
    'focus-visible:zep-ring',
    'focus-visible:zep-ring-3',
    'focus-visible:zep-ring-offset-1',
    'focus-visible:zep-ring-focus',
  ],
  {
    variants: {
      variant: buttonVariants,
    },
    defaultVariants: {
      variant: ButtonVariant.Primary,
    },
  },
);

export const Button: FC<ButtonProps> = ({
  className,
  variant,
  isLoading = false,
  label,
  children,
  icon,
  iconPosition = 'left',
  ...props
}: ButtonProps) => {
  return (
    <button className={twMerge(button({ variant, className }))} {...props}>
      {isLoading && (
        <i className="mr-zep-0_5" data-testid="loading-icon">
          loading icon
        </i>
      )}
      <Spacing gap='0.5' className={iconPosition === 'left' ? 'zep-flex-row-reverse' : ''}>
        {label ?? children}
        {icon && <FunctionalIcon name={icon} />}
      </Spacing>
    </button>
  );
};
